body {
    font-family: 'Source Sans Pro', sans-serif !important;
    font-weight: 400 !important;
    font-size: 1.2em !important;
    margin: 0;
    padding: 0;
}

.btn-custom {
    margin: 4px;
    padding: 0 10px;
    -webkit-transition: -webkit-transform 0.3s ease-in;
    transition: -webkit-transform 0.3s ease-in;
    -o-transition: transform 0.3s ease-in;
    transition: transform 0.3s ease-in;
    transition: transform 0.3s ease-in, -webkit-transform 0.3s ease-in;
}

.btn-custom:hover {
    -webkit-transform: scale(1.07);
        -ms-transform: scale(1.07);
            transform: scale(1.07);
}

.mainColor {
    background: #087683;
}

.secondaryColor, .secondaryColor:hover, .secondaryColor:focus, .secondaryColor:active {
    background: #098a6a;
}

.discountOnColor, .discountOnColor:hover, .discountOnColor:focus, .discountOnColor:active {
    background: #2e7d32;
}

.discountOffColor, .discountOffColor:hover, .discountOffColor:focus, .discountOffColor:active {
    background: #c62828;
}



